.top-bar {
  @include flex-align-center;

  justify-content: space-between;

  &__link {
    @include flex-align-center;
  }

  &__icons {
    display: flex;
    gap: 32px;
  }

  &__logo {
    width: 130px;

    @include hover-increase_active-decrease;

    @include onTablet {
      width: 187px;
    }
  }
}
